                        class="text-none align-self-center"
                        >insertFirst(item)</v-btn
                      >
                    </v-card>

                    <v-card flat class="pa-4 d-flex">
                      <v-text-field
                        @keyup.enter="insertLast"
                        label="item"
                        v-model="insertLastItem"
                        class="flex-grow-1 mr-4"
                      ></v-text-field>
                      <v-btn
                        @click="insertLast"
                        color="primary"
                        class="text-none align-self-center"
                        >insertLast(item)</v-btn
                      >
                    </v-card>

                    <v-card flat class="pa-4 d-flex">
                      <v-text-field
                        label="index"
                        v-model="insertBeforeIndex"
                        class="flex-grow-1 mr-4"
                      ></v-text-field>
                      <v-text-field
                        label="item"
                        v-model="insertBeforeItem"
                        class="flex-grow-1 mr-4"
                      ></v-text-field>
                      <v-btn
                        @click="insertBefore"
                        color="primary"
                        class="text-none align-self-center"
                        >insertBefore(index, item)</v-btn
                      >
                    </v-card>

                    <v-card flat class="pa-4 d-flex">
                      <v-text-field
                        label="index"
                        v-model="insertAfterIndex"
                        class="flex-grow-1 mr-4"
                      ></v-text-field>
                      <v-text-field
                        label="item"
                        v-model="insertAfterItem"
                        class="flex-grow-1 mr-4"
                      ></v-text-field>
                      <v-btn
                        @click="insertAfter"
                        color="primary"
