                        >insertAfter(index, item)</v-btn
                      >
                    </v-card>

                    <v-card flat class="pa-4 d-flex">
                      <v-text-field
                        label="index"
                        v-model="deleteBeforeIndex"
                        class="flex-grow-1 mr-4"
                      ></v-text-field>
                      <v-btn
                        @click="deleteBefore"
