<script>
import { DLinkedList } from "./structures";

export default {
  name: "App",
  data: () => ({
    tab1: "playin-tab",
    list: new DLinkedList(),
    tab2: null,
    item: null,
    output: [],
    maxOutputSize: 10,
    insertFirstItem: null,
    insertLastItem: null,
    insertBeforeItem: null,
    insertBeforeIndex: null,
    insertAfterItem: null,
    insertAfterIndex: null,
    deleteBeforeIndex: null,
    deleteAfterIndex: null
  }),
  methods: {
    deleteFirst() {
      try {
        this.output.push({
          operation: "deleteFirst()",
          result: this.list.deleteFirst()
        });
      } catch (err) {
        this.output.push({
          operation: "deleteFirst()",
          result: err,
          isError: true
        });
      } finally {
        this.regulateOutput();
      }
    },
    deleteLast() {
      try {
        this.output.push({
          operation: "deleteLast()",
          result: this.list.deleteLast()
        });
      } catch (err) {
        this.output.push({
          operation: "deleteLast()",
          result: err,
          isError: true
        });
      } finally {
        this.regulateOutput();
      }
    },
    deleteBefore() {
      if (!this.deleteBeforeIndex) return;
      try {
        this.output.push({
          operation: "deleteBefore()",
          result: this.list.deleteBefore(+this.deleteBeforeIndex)
        });
      } catch (err) {
        this.output.push({
          operation: `deleteBefore(${this.deleteBeforeIndex})`,
          result: err,
          isError: true
        });
      } finally {
        this.deleteBeforeIndex = null;
        this.regulateOutput();
      }
    },
    deleteAfter() {
      if (!this.deleteAfterIndex) return;
      try {
        this.output.push({
          operation: `deleteAfter(${this.deleteAfterIndex})`,
          result: this.list.deleteAfter(+this.deleteAfterIndex)
        });
      } catch (err) {
        this.output.push({
          operation: "deleteAfter()",
          result: err,
          isError: true
        });
      } finally {
        this.deleteAfterIndex = null;
        this.regulateOutput();
      }
    },
    insertFirst() {
      if (!this.insertFirstItem) return;
      this.list.insertFirst(this.insertFirstItem);
      this.output.push({
        operation: `insertFirst(${this.insertFirstItem})`
      });
      this.insertFirstItem = null;
      this.regulateOutput();
    },
    insertLast() {
      if (!this.insertLastItem) return;
      this.list.insertLast(this.insertLastItem);
      this.output.push({
        operation: `insertLast(${this.insertLastItem})`
      });
      this.insertLastItem = null;
      this.regulateOutput();
    },
    insertBefore() {
      if (!this.insertBeforeItem || !this.insertBeforeIndex) return;
      try {
        this.list.insertBefore(+this.insertBeforeIndex, this.insertBeforeItem);
        this.output.push({
          operation: `insertBefore(${this.insertBeforeIndex}, ${this.insertBeforeItem})`
        });
      } catch (err) {
        this.output.push({
          operation: `insertBefore(${this.insertBeforeIndex}, ${this.insertBeforeItem})`,
          result: err,
          isError: true
        });
      } finally {
        this.insertBeforeItem = null;
        this.insertBeforeIndex = null;
        this.regulateOutput();
      }
    },
    insertAfter() {
      if (!this.insertAfterItem || !this.insertAfterIndex) return;
      try {
        this.list.insertAfter(+this.insertAfterIndex, this.insertAfterItem);
        this.output.push({
          operation: `insertAfter(${this.insertAfterIndex}, ${this.insertAfterItem})`
        });
      } catch (err) {
        this.output.push({
          operation: `insertAfter(${this.insertAfterIndex}, ${this.insertAfterItem})`,
          result: err,
          isError: true
        });
      } finally {
        this.insertAfterItem = null;
        this.insertAfterIndex = null;
        this.regulateOutput();
      }
    },
    size() {
      this.output.push({ operation: "size()", result: this.list.size() });
      this.regulateOutput();
    },
    isEmpty() {
      this.output.push({
        operation: "isEmpty()",
        result: this.list.isEmpty()
      });
      this.regulateOutput();
    },
    allItems() {
      this.output.push({
        operation: "getItems()",
        result: [...this.list.getItemsAsArray()]
      });
      this.regulateOutput();
    },
    regulateOutput() {
      if (this.output.length > this.maxOutputSize) {
        this.output.shift();
      }
    }
  }
};
</script>

